import Vue from 'vue'
import Router from 'vue-router'
import TakeOutFood from '../pages/take-out-food'
// import Search from '../pages/search'
// import Order from '../pages/order'
// import PersonalCenter from '../pages/personal-center'
// import Business from '../pages/business'
// import Shops from '../pages/shops'
// import Positioning from '../pages/positioning'
// import positionSearch from '../pages/position-search'
// import InfoShop from '../pages/info-shop';
//异步加载，让webpack打包的时候，分开打包，不是吧该组件打包到一个文件里
//缺点：增加文件数目，让http请求增多
//优点：运算速度快，减少流量，首页加载快，用户体验好
//一般在加载页面时使用异步加载

Vue.use(Router)

export default new Router({
  routes: [
	{
		path:'/take-out-food',
		name:'TakeOutFood',
		component:TakeOutFood
	},
	{
		path:'/search',
		name:'Search',
		component:import('../pages/search.vue')
	},
	{
		path:'/shops',
		name:'Shops',
		component:import('../pages/shops.vue'),
		children:[
			{
				path:':id',
				component:import('../pages/shops.vue'),
			}
		]
	},
	{
		path:'/order',
		name:'Order',
		component:import('../pages/order.vue'),
	},
	{
		path:'/personal-center',
		name:'PersonalCenter',
		component:import('../pages/personal-center.vue')
	},
	{
		path:'/business',
		name:'Business',
		component:import('../pages/business.vue')
	},
	{
		path:'/positioning',
		name:'Positioning',
		component:import('../pages/positioning.vue')
	},
	{
		path:'/position-search',
		name:'positionSearch',
		component:import('../pages/position-search.vue')
		
	},
	{
		path:'/info-shop',
		name:'InfoShop',
		component:import('../pages/info-shop.vue')
	},
	{
		path:'*',
		redirect:'/take-out-food'
	}
  ]
})



