import Vuex from 'vuex';
import Vue from 'vue';
Vue.use(Vuex);

export default new Vuex.Store({
	state:{
		//盛放全局变量
		locationInfo:{
			"pinyin":"",
			"is_map":true,
			"longitude":null,
			"latitude":null,
			"sort":null,
			"area_code":"",
			"abbr":"",
			"name":"",
			"id":null
		},
		position:''
	},
	mutations:{
		//改变全局变量的函数
		changeLocationInfo(state,position){
			state.locationInfo={
				"pinyin":"",
				"is_map":true,
				"longitude":position.point.lng,
				"latitude":position.point.lat,
				"sort":null,
				"area_code":"",
				"abbr":"",
				"name":position.addressComponents.city,
				"id":null
			};
		},
		changeLocationInfo1(state,position){
			state.locationInfo=position;
		},
		changeLocationInfo2(state,position){
			state.locationInfo=position;
		}
	}
});



